:root {
  --primary: #213555;
  --secondary: #4f709c;
  --accent: #d8c4b6;
  --main: #f5efe7;
}

.hero {
  color: var(--main);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 7rem 12rem;
  gap: 6rem;
  /* height: 100vh; */
}

.hero-details {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6rem;
  padding: 1.5rem;
}

.hero-about {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans";
}

.hero-name {
  font-size: 2.4rem;
  font-weight: bold;
  margin-bottom: 1.1rem;
}

.nickname {
  color: var(--blue);
  font-weight: bolder;
}

.hero-info {
  font-size: 1.25rem;
  font-weight: 600;
}

#profile {
  width: 20rem;
  margin: 2rem;
  border-radius: 50%;
  object-fit: cover;
}
